import useCustomMember from '@hooks/useCustomMember';
import React, { useEffect } from 'react';
import useCustomWorkspace from '@hooks/useCustomWorkspace';
import { toast } from 'react-toastify';
import { removeCookie } from '@utils/cookieUtil';
import { useParams } from 'react-router';

const PrivateRoute: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const { isLogin, memberState, deleteSlices, moveToLogin, isInitialLoading, moveToPath, updateSlices } =
    useCustomMember();
  const { workspaceState } = useCustomWorkspace();
  const { url } = useParams<{ url: string }>();

  useEffect(() => {
    const checkLoginStatus = async () => {
      if (isInitialLoading) return;
      const workspaces = memberState?.workspaces;
      // 쿠키 -> 기본값을 로드 X -> 로그아웃
      if (!isLogin || !workspaceState || !Array.isArray(workspaces) || workspaces.length === 0 || !workspaces[0].name) {
        console.log('workspaces[0].name', workspaces[0].name);
        try {
          removeCookie('member');
          await deleteSlices();
          moveToLogin();
        } catch (error) {
          toast.error('Failed to log out');
        }
      }
      // 로그인 O url X
      const isUrlValid = workspaces.some((workspace) => workspace.url === url);
      if (!isUrlValid) {
        moveToPath('/not-found');
      }
    };
    checkLoginStatus().then();
  }, [deleteSlices, isInitialLoading, isLogin, memberState?.workspaces, moveToLogin, moveToPath, url, workspaceState]);

  // 로그인 O url O -> url 변경 -> slice update
  // isInitialLoading === true 여도 url 은 충분히 변경될 수 있으므로
  useEffect(() => {
    if (isLogin && url) {
      updateSlices(url).then();
    }
  }, [isLogin, updateSlices, url]);

  return <>{children}</>;
};

export default PrivateRoute;
